// ------------------------------------------------------
// TYPE
// ------------------------------------------------------

// Headings
// ------------------------------------------------------
h1, h2, h3, h4, h5, h6 {
  margin-bottom: 1rem;
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
  color: $headings-color;
}

h1 { font-size: rem(24px) }
h2 { font-size: rem(14px) }

h3, h4, h5, h6 {
  font-size: rem(12px);
  text-transform: uppercase;
}


// Emphasis
// ------------------------------------------------------
small,
.small {
  font-size: 80%;
}

