// ----------------------------------------------------------------------
// VARIABLES
// ----------------------------------------------------------------------


// Color system
// ----------------------------------------------------------------------
$black:    #000;
$gray-100: #090909;
$gray-200: #31343a;
$gray-300: #8b8683;
$gray-400: #9ea0a5;
$white:    #fff;

$blue-100: #1991cf;
$blue-200: #269fdd;
$blue-300: #40a8de;
$blue-400: #77b0ca;
$blue-500: #53bbf0;
$blue-600: #77b0ca;
$blue-700: #91cde8;
$blue-800: #d2e9f5;
$blue-900: #e4f5fd;

$blue-bayoux-100: #2f4956;
$blue-bayoux-200: #406071;

$red-100:   #e89191;
$red-200:   #f8bfbf;
$red-300:   #fac1c1;

$green-100:   #28ddc9;
$green-200:   #c4f1ec;
$green-300:   #cef7f3;

$primary:    $blue-100;
$success:    $green-100;
$danger:     $red-100;


// Body
// ----------------------------------------------------------------------
// Settings for the `<body>` element.
$body-bg:                   $white;
$body-color:                $gray-200;


// Links
// ----------------------------------------------------------------------
// Style anchor elements.
$link-color:                              $blue-100;
$link-decoration:                         none;
$link-hover-color:                        darken($link-color, 15%);
$link-hover-decoration:                   underline;


// Paragraphs
// ----------------------------------------------------------------------
// Style p element.
$paragraph-margin-bottom:   1rem;


// Grid
// ----------------------------------------------------------------------
$container-max-width:   1280px;
$grid-columns:          4;
$grid-gutter-width:     30px;


// Typography
// ----------------------------------------------------------------------
$font-family-sans-serif:      Gotham, "Helvetica Neue", Arial, sans-serif;
$font-family-monospace:       SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
$font-family-base:            $font-family-sans-serif;

$font-size-base:              0.875rem; // Assumes the browser default, typically `16px`

$font-weight-light:           300;
$font-weight-normal:          400;
$font-weight-bold:            600;

$font-weight-bolder:          $font-weight-bold;
$font-weight-base:            $font-weight-light;

$line-height-base:            1.9;

$headings-font-family:        $font-family-base;
$headings-font-weight:        500;
$headings-line-height:        1.2;
$headings-color:              $body-color;


// Border
// ----------------------------------------------------------------------
$border-radius-base:          4px;
$border-color-base:           #e2e5ed;


// Form
// ----------------------------------------------------------------------
$input-height:                2.625rem;
$input-border-color:          $border-color-base;
$input-border-radius:         0;
$input-color:                 $gray-400;
$label-color:                 $gray-400;
$label-margin-bottom:         .5rem;