// -----------------------------------------------------------------------------
// FONT
// -----------------------------------------------------------------------------

$font-path: "../font" !default;


// Gotham Light
// -----------------------------------------------------------------------------
@font-face {
  font-family: 'Gotham';
  src: url('#{$font-path}/Gotham-Light.woff2') format('woff2'),
  url('#{$font-path}/Gotham-Light.woff') format('woff'),
  url('#{$font-path}/Gotham-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}


// Gotham Book
// -----------------------------------------------------------------------------
@font-face {
  font-family: 'Gotham';
  src: url('#{$font-path}/Gotham-Book.woff2') format('woff2'),
  url('#{$font-path}/Gotham-Book.woff') format('woff'),
  url('#{$font-path}/Gotham-Book.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}


// Gotham Medium
// -----------------------------------------------------------------------------
@font-face {
  font-family: 'Gotham';
  src: url('#{$font-path}/Gotham-Medium.woff2') format('woff2'),
       url('#{$font-path}/Gotham-Medium.woff') format('woff'),
       url('#{$font-path}/Gotham-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}


// Gotham Bold
// -----------------------------------------------------------------------------
@font-face {
  font-family: 'Gotham';
  src: url('#{$font-path}/GothamBold.woff2') format('woff2'),
  url('#{$font-path}/GothamBold.woff') format('woff'),
  url('#{$font-path}/GothamBold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}
