// ------------------------------------------------------
// FORN
// ------------------------------------------------------

.form-control,
.form-text,
.form-select,
.form-textarea {
  display: block;
  width: 100%;
  border: 1px solid $input-border-color;
  border-radius: $input-border-radius;
  padding: rem(6px 12px);
  background-color: $white;
  background-clip: padding-box;
  color: $body-color;
  font-size: $font-size-base;
  font-family: $font-family-base;
  font-weight: 400;
  box-shadow: inset 0 1px 2px rgba(#66717B, .2);
}

textarea.form-control,
.form-textarea {
  height: rem(97px);
}

.form-control {
  &--flag {
    border-left: 17px solid $blue-700;
  }
}


// Custom option
// ------------------------------------------------------
.form-option {
  position: relative;
  display: inline-flex;
  height: $input-height;
  border: 1px solid $blue-800;
  border-radius: 0 $border-radius-base $border-radius-base 0;
  padding-right: rem(40px);
  background-color: $blue-900;
  line-height: 1.2;
  overflow: hidden;

  &__control {
    position: absolute;
    left: -999rem;
  }

  &__label {
    align-self: center;
    color: $blue-400;
    font-weight: 500;

  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 21px;
    margin-right: rem(7px);
    background-color: $blue-700;
    color: #fff;
    svg {
      height: 11px;
    }
  }
}